import { DragHandlerProps, DragSizingData, DragSizingProps } from './types';

// eslint-disable-next-line @typescript-eslint/no-explicit-any
export const isNil = (v: any) => v === null || v === undefined;

export const getContainerMeta = ({
  border,
}: {
  border: DragSizingProps['border'];
}) => {
  let wh: 'width' | 'height';
  let xy: 'clientX' | 'clientY';
  let sn: 1 | -1;

  if (/^(left|right)$/.test(border)) {
    wh = 'width';
    xy = 'clientX';
    sn = border === 'right' ? 1 : -1;
  } else {
    wh = 'height';
    xy = 'clientY';
    sn = border === 'bottom' ? 1 : -1;
  }
  return { wh, xy, sn };
};

export const getContainerInfo = ({
  style,
  containerMeta,
  diffCoord,
  oldSize,
}: {
  style: DragSizingProps['style'];
  containerMeta: ReturnType<typeof getContainerMeta>;
  diffCoord: DragSizingData['diffCoord'];
  oldSize: DragSizingData['oldSize'];
}) => {
  const { wh, sn } = containerMeta;
  let retStyle: React.CSSProperties = {};

  if (oldSize != null) {
    retStyle[wh] = oldSize + diffCoord * sn;
  }
  retStyle = {
    ...style,
    ...retStyle,
  };
  return { style: retStyle };
};

export const getHandlerInfo = ({
  border,
  handlerWidth,
  handlerOffset,
  handlerStyle,
}: {
  border: DragSizingProps['border'];
  handlerWidth: DragSizingProps['handlerWidth'];
  handlerOffset: DragSizingProps['handlerOffset'];
  handlerStyle: DragSizingProps['handlerStyle'];
}) => {
  let dir: DragHandlerProps['dir'];
  let style: React.CSSProperties = {};

  if (/^(left|right)$/.test(border)) {
    dir = 'ew';
    style.width = handlerWidth;
    style.top = 0;
    style.bottom = 0;
  } else {
    dir = 'ns';
    style.height = handlerWidth;
    style.left = 0;
    style.right = 0;
  }
  style[border] = handlerOffset;

  style = { ...style, ...handlerStyle };
  return { dir, style };
};
